<template>
    <div class="container">
        <v-back title="历史未归" />
        <div class="form">
            <md-field>
                <md-field-item title="选择日期" :content="selectorValue1" @click="showSelector1" arrow solid />
				<md-field-item title="选择学院" :content="selectorValue3" @click="showSelector3" arrow solid />
				<md-field-item
					v-show="selectorValue3 != '' && selectorValue3 != '请选择'"
					  title="选择专业"
					  :content="selectorValue5"
					  @click="showSelector5"
					  arrow
					  solid
					/>
					<md-field-item
					v-show="selectorValue3 != '' && selectorValue3 != '请选择'"
					  title="选择班级"
					  :content="selectorValue6"
					  @click="showSelector6"
					  arrow
					  solid
					/>
				
                <md-field-item title="选择宿舍楼" :content="selectorValue2" @click="showSelector2" arrow solid />
                <md-field-item title="选择房间号" :content="selectorValue4" @click="showSelector4" arrow solid v-show="selectorValue2 != '' && selectorValue2 != '请选择'" />
			</md-field>
            <md-date-picker
                ref="datePicker"
                v-model="isSelectorShow1"
                type="custom"
                title="选择日期"
				:max-date="maxDate"
                large-radius
                :custom-types="['yyyy', 'MM','dd']"
                :default-date="currentDate"
                @confirm="onSelectorConfirm1"></md-date-picker>
            <md-selector v-model="isSelectorShow2" :data="data2" @confirm="onSelectorConfirm2" min-height="320px" max-height="320px" title="选择宿舍楼" okText="确认" large-radius></md-selector>
            <md-selector v-model="isSelectorShow3" :data="data3" @confirm="onSelectorConfirm3" min-height="320px" max-height="320px" title="选择学院" okText="确认" large-radius></md-selector>
            <md-selector v-model="isSelectorShow4" :data="data4" @confirm="onSelectorConfirm4" min-height="320px" max-height="320px" title="选择房间号" okText="确认" large-radius></md-selector>
			<md-selector
			    v-model="isSelectorShow5"
			    :data="data5"
			    min-height="320px"
			    max-height="320px"
			    title="选择专业"
			    okText="确认"
			    large-radius
			    @confirm="onSelectorConfirm5"
			    >
			</md-selector>
			<md-selector
			    v-model="isSelectorShow6"
			    :data="data6"
			    min-height="320px"
			    max-height="320px"
			    title="选择班级"
			    okText="确认"
			    large-radius
			    @confirm="onSelectorConfirm6"
			    >
			</md-selector>
			<md-field>
                <md-button type="primary" size="small" round @click="submit((()=>{}))">查询</md-button>
                <md-landscape v-model="showFullScreen" :full-screen="true">
                    <div style="text-align: left;padding: 4vw;font-size: 4vw;line-height: 6vw;">
                        <div style="margin-bottom: 4vw;">详细信息</div>
                        <div class="flex align_center" style="background-color: #1acfc6;box-sizing: border-box;padding: 2vw;border-radius: 1vw;">
                            <img v-if="showData.photo" :src="showData.photo" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;"  @click="imgsShow(showData.photo)">
                            <img v-else src="@/assets/student.png" alt="" style="box-sizing: border-box;width: 30vw;margin: 0 auto;display: block;box-shadow: 2px 2px 5px rgba(0,0,0,.2);border: 1px solid #f5f5f5;">
                    		<div class="flex btns">
                    		    <div>
                    		        <div @click="statusChange(1)">在寝</div>
                    		    </div>
                    		    <div>
                    		        <div @click="statusChange(0)">未归</div>
                    		    </div>
                    		    <div>
                    		        <div @click="statusChange(2)">实习</div>
                    		    </div>
                    		    <div>
                    		        <div @click="statusChange(3)">请假</div>
                    		    </div>
                    			<!-- <div>
                    			    <div @click="statusChange">毕业</div>
                    			</div> -->
                    		</div>
                    	</div>
                    	<div class="person_list_text">
                    		<div class="flex">
                    			当时状态：
                    			<div class="full_status">
                    				{{ personStatusText(showData) }} | 
                    				<span :class="statusText(showData) == '在寝' ? 'active' : (statusText(showData) == '未归' ? '' : 'noactive')">{{statusText(showData)}}</span>
                    			</div>
                    		</div>
                    		<div class="flex align_center" v-show="personStatusText(showData) != '正常'">请假/实习：{{aroundTime}} <md-icon name="id-card" size="lg" @click="imgsShow(showData.person_io_leave_practices[0].photo)"></md-icon></div>
                    		<div class="flex">抓拍图片：<img v-show="showData.snap_photo" style="width: 15vw;height: 15vw;" @click="imgsShow(showData.snap_photo)" :src="showData.snap_photo" alt=""></div>
                    		<div>学生姓名：{{showData.student_name}}</div>
                    		<div>学生学号：{{showData.student_nbr}}</div>
                    		<div>学院名称：{{showData.org_names}}</div>
                    		<div>班级名称：{{showData.class_names || '--'}}</div>
                    		<div>住宿信息：{{showData.building_name}}{{showData.dorm_nbr || '--'}}间{{showData.sys_user ? showData.sys_user.bed_nbr : '--'}}床</div>
                    		<div>当时通行：<span style="font-size: 3.6vw;font-weight: bolder;" :style="showData.entry_exit_type == '1' ? 'color: #1acfc6;' : showData.entry_exit_type == '2' ? 'color: red;' : 'color: #ccc;'">({{showData.entry_exit_type == '1' ? '进' : showData.entry_exit_type == '2' ? '出' : '未知'}})</span>{{showData.snap_camera_name}} {{showData.entry_exit_time}}</div>
                    		<div>备注：{{showData.modify_in_dorm_status_logs && showData.modify_in_dorm_status_logs.length > 0 ? '在寝状态由【' + showData.modify_in_dorm_status_logs[0].modify_user_name + '】在【' + showData.modify_in_dorm_status_logs[0].modify_time +'】修正过' : '--'}}</div>
                    	</div>
                    </div>
                </md-landscape>
            </md-field>
            <div style="font-size: 4vw;text-align: left;line-height: 6vw;padding: 4vw 4vw 0;color: #666;">
				
				<div class="list" v-for="(item ,index) in list" :key="index" @click="fullShow(item, index)">
					<div class="flex">
						<img v-if="item.photo" :src="item.photo" alt="" @click="imgsShow(item.photo)">
						<img v-else src="@/assets/student.png" alt="">
						<div>
							<div class="flex"><h4>{{item.student_name}}</h4><span>[{{statusText(item)}}]</span></div>
							<div>{{item.college_name}}{{item.org_names}}{{item.class_names}}</div>
							<div>{{item.building_name}}{{item.dorm_nbr}}间{{item.sys_user ? item.sys_user.bed_nbr : item.bed_nbr}}床</div>
						</div>
					</div>
				</div>
				<div v-if="list.length == 0" style="background: #fff;margin: 4vw 0 0;padding: 2vw 0;">
					<md-result-page></md-result-page>
				</div>
            </div>
        </div>
		<!-- 图片预览 -->
		<md-image-viewer
			v-model="isViewerShow"
			:list="imgs"
			:has-dots="true">
		</md-image-viewer>
    </div>
</template>

<script>
    import dayjs from 'dayjs'
	export default {
		name: 'historyunback_index',
		data() {
			return { 
				isViewerShow: false,
				imgs: [],
				
                isSelectorShow1: false,
                data1: [],
                selectorValue1: '',
                value1: '',
                isSelectorShow2: false,
                data2: [],
                selectorValue2: '',
                value2: '',
                isSelectorShow3: false,
                data3: [],
                selectorValue3: '',
                value3: '',
                isSelectorShow4: false,
                data4: [],
                selectorValue4: '',
                value4: '',
				
				isSelectorShow5: false,
				data5: [],
				selectorValue5: '',
				value5: '',
				
				isSelectorShow6: false,
				data6: [],
				selectorValue6: '',
				value6: '',

                showFullScreen: false,
                list: [],
				aroundTime: '',

                showData: {},

                currentDate: new Date(),
				maxDate: new Date(),
				
				index: 0
            }
        },
        computed: {
            lastPassTime() {
                return dayjs(this.showData.entry_exit_time).format('YYYY-MM-DD HH:mm:ss')
            }
        },
        methods: {
			imgsShow(img) {
				this.isViewerShow = true
				this.imgs = [img]
			},
			statusText(item) {
				if (item.modify_in_dorm_status_logs && item.modify_in_dorm_status_logs.length > 0) {
					if (item.modify_in_dorm_status_logs[0].in_dorm_status == null) {
						return '无记录'
					} else {
						if (item.modify_in_dorm_status_logs[0].in_dorm_status == '0') {
							return '未归'
						} else {
							if (item.modify_in_dorm_status_logs[0].in_dorm_status == '1') {
								return '在寝'
							} else {
								return '未知'
							}
						}
					}
				} else {
					if (item.entry_exit_type == '1') {
						return '在寝'
					} else if (item.entry_exit_type == '2') {
						return '未归'
					} else {
						return '无记录'
					}
				}
			},
			personStatusText(item) {
				let text = '无记录'
				if (item.entry_exit_status != null) {
					if (item.entry_exit_status == '0') {
						text = '正常'
					}
				}
				if (item.person_io_leave_practices && item.person_io_leave_practices.length > 0) {
					this.aroundTime = dayjs(item.person_io_leave_practices[0].no_attend_starttime).format('YYYY-MM-DD') + ' ~ ' + dayjs(item.person_io_leave_practices[0].no_attend_endtime).format('YYYY-MM-DD')
					if (item.person_io_leave_practices[0].type == 1) {
						text = '请假'
					} else {
						text = '实习'
					}
				}
				return text
			},
            showSelector1() {
                this.isSelectorShow1 = true
            },
            showSelector2() {
                this.isSelectorShow2 = true
            },
            showSelector3() {
                this.isSelectorShow3 = true
            },
            showSelector4() {
                this.isSelectorShow4 = true
            },

            onSelectorConfirm1() {
                this.value1 = this.$refs.datePicker.getFormatDate('yyyy-MM-dd')
                this.selectorValue1 = this.$refs.datePicker.getFormatDate('yyyy-MM-dd')
            },
            onSelectorConfirm2({text, value}) {
                this.value2 = value
                this.selectorValue2 = text
                this.getHostel()
            },
            onSelectorConfirm3({text, value}) {
                this.value3 = value
                this.selectorValue3 = text
				
				this.value5 = ''
				this.selectorValue5 = ''
				this.value6 = ''
				this.selectorValue6 = ''
				this.data6 = []
				if (value != '') {
				    this.getDepartment(this.value3, 1)
				} else {
				    this.data5 = []
				}
				
            },
            onSelectorConfirm4({text, value}) {
                this.value4 = value
                this.selectorValue4 = text
            },
			
			showSelector5() {
			    this.isSelectorShow5 = true
			},
			onSelectorConfirm5({text, value}) {
			    this.value5 = value
			    this.selectorValue5 = text
				this.value6 = ''
				this.selectorValue6 = ''
				if (value != '') {
				    this.getDepartment(this.value5, 2)
				} else {
				    this.data6 = []
				}
			},
			showSelector6() {
			    this.isSelectorShow6 = true
			},
			onSelectorConfirm6({text, value}) {
			    this.value6 = value
			    this.selectorValue6 = text
			},

            
            //打开详情
            fullShow(itm, index) {
				this.index = index
                this.showData = itm
                this.showFullScreen = true
            },
            //改变状态
			statusChange(status) {
				this.$getL('/dorm/last_change', {
				    userId: JSON.parse(localStorage.getItem('user')).user_id,
					username: JSON.parse(localStorage.getItem('user')).username,
				    studentNbr: this.showData.student_nbr,
					entry_exit_time: this.showData.entry_exit_time,
				    status: status
				}, (res)=>{
				    this.$toast.succeed('修改成功')
					setTimeout(() => {
						this.$toast.hide()
						this.submit(() => {
							this.showData = JSON.parse(JSON.stringify(this.list[this.index]))
						})
					}, 500)
				})
			},
            //查询
            submit(callback) {
                if (this.value1 == '') {
                    this.$toast.failed('请选择日期')
                } else {
                    this.$getL('/history/list', {
                        userId: JSON.parse(localStorage.getItem('user')).user_id,
                        entryExitTime: this.value1,
                        buildingId: this.value2,
                        college_id: this.value3,
                        dormId: this.value4,
						org_ids: this.value5,
						class_ids: this.value6
                    }, (res)=>{
                        this.list = res.data
                        this.$toast.hide()
						if (callback) {
							callback()
						}
                    })
                }
            },
            //获取宿舍楼栋
            getFloor(callback) {
                this.$getL('/dorm/floor', {
					userId: JSON.parse(localStorage.getItem('user')).user_id
				}, (res)=>{
                    this.data2 = []
                    res.data.forEach((item) => {
                        this.data2.push({
                            value: item.buildingId,
                            text: item.buildingName
                        })
                    })
					if (callback) callback()
				})
            },
            //获取学院
            getDepartment(parent_org_id, type) {
            	let formData = {
            		userId: JSON.parse(localStorage.getItem('user')).user_id
            	}
            	if (parent_org_id != '') {
            		formData.parent_org_id = parent_org_id
            	}
            	if (type == 2) {
            		this.$getL('/college/class', {
            			org_id: parent_org_id
            		}, (res => {
            			this.data6 = [{
            			    value: '',
            			    text: '请选择'
            			}]
            			res.data.forEach((item) => {
            			    this.data6.push({
            			        value: item.id,
            			        text: item.bj_name
            			    })
            			})
            			this.$toast.hide()
            		}))
            		return;
            	}
            	
                this.$getL('/college/department', formData, (res)=>{
            		if (type == 0) {
            			console.log('666')
            			this.data3 = [{
            			    value: '',
            			    text: '请选择'
            			}]
            			res.data.forEach((item) => {
            			    this.data3.push({
            			        value: item.orgId,
            			        text: item.orgName
            			    })
            			})
            		}
            		if (type == 1) {
            			console.log('7777')
            			this.data5 = [{
            			    value: '',
            			    text: '请选择'
            			}]
            			res.data.forEach((item) => {
            			    this.data5.push({
            			        value: item.orgId,
            			        text: item.orgName
            			    })
            			})
            		}
                    this.$toast.hide()
                })
            },
             //获取房间号
             getHostel() {
                this.$getL('/dorm/hostel', {
                    buildingId: this.value2
                }, (res)=>{
                    this.value4 = ''
                    this.selectorValue4 = ''
                    this.data4 = [{
                        value: '',
                        text: '请选择'
                    }]
                    res.data.forEach((item) => {
                        this.data4.push({
                            value: item.dormId,
                            text: item.dormNbr
                        })
                    })
                    this.$toast.hide()
                })
            }
        },
        mounted() {
			this.selectorValue1 = dayjs().format('YYYY-MM-DD')
			this.value1 = dayjs().format('YYYY-MM-DD')
            this.getFloor(() => {
				setTimeout(() => {
					this.getDepartment('', 0)
				}, 200)
			})
        }
    }
</script>

<style scoped>
    .container {
        height: 100vh;
        overflow-y: auto;
        background-color: #fff;
    }
    .list {
        padding: 3vw;
        margin-bottom: 4vw;
        border: 1px solid #e2e4ea;
    }
    .list h4 {
    	margin-right: 2vw;
    }
    .list img {
    	width: 20vw;
    	height: 23vw;
    	margin-right: 4vw;
    }
    .list span.active {
        color: #2f86f6;
    }
    .list span.noactive {
        color: #ccc;
    }
    .list span {
        color: #fdb731;
    }
    .tab_active {
    	background-color: #1acfc6;
    	color: #fff;
    	border-radius: 1vw;
    }
</style>